import { VariantType } from "notistack"
import { styled } from "@rp/react/theme"
import { IconButton, SnackbarContent } from "@rp/react/web/mui/core"

type ExtraProps = {
  variantName: VariantType
  disableClose?: boolean
}

const SnackbarContentStyled = styled(SnackbarContent, {
  shouldForwardProp: prop => prop !== "variantName" && prop !== "disableClose",
})<ExtraProps>(({ theme, variantName, disableClose }) => {
  const variantColors = {
    success: theme.palette.success.main,
    error: theme.palette.error.main,
    warning: theme.palette.warning.main,
    info: theme.palette.info.main,
    default: theme.palette.grey[100],
  }
  return {
    color: theme.palette.grey[900],
    backgroundColor: variantColors[variantName],
    "& > div": {
      maxWidth: disableClose ? "100%" : "calc(100% - 42px)",
    },
  }
})

const SnackbarMessageStyled = styled("div", {
  shouldForwardProp: propName => propName !== "disableClose",
})<{ disableClose?: boolean }>(({ theme, disableClose }) => ({
  display: "flex",
  gap: theme.spacing(2),
  alignItems: "center",
  maxWidth: disableClose ? 320 : 300,
}))

const CloseButtonStyled = styled(IconButton)(({ theme }) => ({
  color: theme.palette.grey[900],
}))

export { CloseButtonStyled, SnackbarContentStyled, SnackbarMessageStyled }
